* {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

body {
  position: relative;
}

input {
  all: unset;
}

button {
  border: none;
  background: none;
  cursor: pointer;
}

ul {
  list-style: none;
}

image {
  width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

.features__item::after, .productive__link::before, .productive__link-icon, .testimonials__items::after, .footer__list-link::after, .btn::after, .btn::before, .main__wrapper::after, .menu__list-link::after {
  position: absolute;
  content: "";
}

.access__input::placeholder, .access__input-error, .footer__location, .footer__list-link, .footer__menu-link, body, .menu__list-link {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
}

.testimonials__avatar, .access__title {
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
}

.features__item-title, .btn, .title {
  font-family: "Raleway", sans-serif;
  font-weight: 700;
}

html {
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
}

body {
  color: white;
  font-size: 14px;
  line-height: 170%;
  min-width: 320px;
  overflow-x: hidden;
}

input {
  all: inherit;
}

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

p {
  padding: 0;
  margin: 0;
}

figure {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

button {
  outline: none;
  background-color: transparent;
}
button:active, button:focus {
  outline: none;
}

.btn {
  display: inline-block;
  border-radius: 50px;
  color: white;
  font-size: 14px;
  position: relative;
  z-index: 10;
}
.btn::after, .btn::before {
  width: 100%;
  height: 100%;
  right: 0;
  top: 0;
  left: 0;
  bottom: 0;
  border-radius: 50px;
  transition: all 0.3s;
}
.btn::after {
  background: linear-gradient(90deg, rgba(129, 217, 212, 0) 0%, #339ecc 82%);
  z-index: -2;
}
.btn::before {
  background-color: #81d9d4;
  z-index: -4;
}
.btn:hover::after {
  opacity: 0;
}

.container {
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 15px;
}

.title {
  font-size: 32px;
  line-height: 124%;
}

.logo {
  display: block;
}
.logo__img {
  width: 176px;
  height: 52px;
}

.main__wrapper {
  position: relative;
}
.main__wrapper::after {
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
  background-color: #181f2a;
}

.menu__list {
  display: flex;
}
.menu__list-item + .menu__list-item {
  margin-left: 58px;
}
.menu__list-link {
  color: white;
  font-size: 14px;
  text-transform: capitalize;
  padding-bottom: 1px;
  position: relative;
}
.menu__list-link::after {
  bottom: 0;
  left: 0;
  width: 0;
  height: 1px;
  background-color: white;
  transition: all 0.2s linear;
}
.menu__list-link:hover::after {
  width: 100%;
}

