.header {
  padding-bottom: 160px;
  background: #1c2431 url(../images/bg-curvy-desktop.svg) bottom/100% 420px no-repeat;
}
.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px 0;
}
.header__content {
  text-align: center;
  max-width: 620px;
  margin: 0 auto;
}
.header__img {
  object-fit: cover;
  width: 100%;
  margin: 20px 0;
}
.header__title {
  margin-bottom: 40px;
}
.header__text {
  font-size: 16px;
  max-width: 480px;
  margin: 0 auto 30px;
}
.header__btn {
  padding: 16px 96px;
}

.features {
  padding: 0 0 65px;
}
.features__items {
  display: grid;
  grid-template-columns: repeat(2, minmax(300px, auto));
  grid-gap: 145px 80px;
  justify-content: center;
}
.features__item {
  padding-top: 100px;
  position: relative;
  text-align: center;
  max-width: 330px;
}
.features__item::after {
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  background-size: contain;
  background-repeat: no-repeat;
}
.features__item--access::after {
  background-image: url(../images/icon-access-anywhere.svg);
  width: 83px;
  height: 78px;
}
.features__item-title {
  font-size: 16px;
  margin-bottom: 5px;
}
.features__item-text {
  font-size: 12px;
  line-height: 150%;
  opacity: 0.9;
}
.features__item--security::after {
  background-image: url(../images/icon-security.svg);
  width: 70px;
  height: 88px;
}
.features__item--collab::after {
  background-image: url(../images/icon-collaboration.svg);
  width: 86px;
  height: 71px;
}
.features__item--store::after {
  background-image: url(../images/icon-any-file.svg);
  width: 91px;
  height: 64px;
}

.productive {
  padding: 65px 0;
}
.productive__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.productive__img {
  object-fit: cover;
  max-width: 611px;
  width: 100%;
}
.productive__content {
  max-width: 500px;
  margin-left: 50px;
}
.productive__title {
  margin-bottom: 15px;
  max-width: 400px;
}
.productive__text {
  font-size: 14px;
  margin-bottom: 10px;
}
.productive__link {
  padding: 0 20px 6px 0;
  position: relative;
  color: #65e2d9;
  transition: all 0.2s linear;
}
.productive__link::before {
  right: 0;
  left: 0;
  bottom: 0;
  height: 1px;
  width: 100%;
  background-color: #65e2d9;
  transition: all 0.5s;
}
.productive__link:hover {
  color: white;
}
.productive__link:hover .productive__link-icon g {
  fill: white;
}
.productive__link:hover::before {
  background-color: white;
}
.productive__link-icon {
  fill: #1c2431;
  right: 0;
  top: 2px;
}
.productive__link-icon g {
  transition: all 0.2s linear;
  fill: #65e2d9;
}

.testimonials {
  padding-bottom: 195px;
}
.testimonials__items {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  grid-gap: 40px;
  padding: 35px 10px 0;
  position: relative;
}
.testimonials__items::after {
  background: url(../images/bg-quotes.png) no-repeat;
  top: 0;
  left: 0;
  width: 55px;
  height: 45px;
  z-index: -1;
}
.testimonials__item {
  border-radius: 10px;
  background-color: #202a3c;
  padding: 35px 25px;
  color: white;
  box-shadow: 0px 4px 13px 0px rgba(0, 0, 0, 0.35);
  transition: all 0.4s;
}
.testimonials__text {
  margin-bottom: 25px;
  line-height: 170%;
}
.testimonials__descr {
  line-height: 100%;
}
.testimonials__avatar {
  border-radius: 50%;
  width: 25px;
  height: 25px;
  object-fit: cover;
  display: inline-block;
  float: left;
  margin-right: 8px;
}
.testimonials__author {
  margin-bottom: 5px;
  font-size: 12px;
}
.testimonials__prof {
  font-size: 8px;
}

.access__inner {
  border-radius: 10px;
  background-color: #1c2431;
  text-align: center;
  padding: 40px 75px;
  max-width: 860px;
  margin: 0 auto;
  box-shadow: 0px 4px 13px 0px rgba(0, 0, 0, 0.35);
}
.access__title {
  margin-bottom: 20px;
}
.access__text {
  max-width: 621px;
  margin: 0 auto 30px;
}
.access__form {
  display: flex;
}
.access__input-email {
  position: relative;
  width: 100%;
  margin-right: 30px;
  max-width: 480px;
  width: 100%;
  transition: all 0.2s linear;
}
.access__input {
  border-radius: 50px;
  background-color: white;
  padding: 14px 35px;
  width: 100%;
  font-size: 14px;
  color: #1c2431;
  text-align: left;
}
.access__input:active, .access__input:focus {
  outline: none;
}
.access__input::placeholder {
  color: #c4c4c4;
  font-size: 14px;
}
.access__input-error {
  color: #ff4242;
  font-size: 10px;
  position: absolute;
  bottom: -25px;
  left: 24px;
}
.access__btn {
  padding: 16px 30px;
  flex-shrink: 0;
  position: relative;
}

.footer {
  padding: 190px 0 80px;
  background-color: #0b1523;
  margin-top: -115px;
}
.footer__logo {
  display: block;
  margin-bottom: 30px;
}
.footer__items {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 50px 96px;
}
.footer__location {
  padding-left: 40px;
  position: relative;
  color: white;
  max-width: 368px;
  flex-shrink: 0;
}
.footer__location::after {
  position: absolute;
  content: "";
  width: 13px;
  height: 18px;
  background: url(../images/icon-location.svg) no-repeat;
  left: 0;
  top: 0;
}
.footer__list-item {
  margin-bottom: 20px;
}
.footer__list-link {
  position: relative;
  padding: 0 0 0 44px;
  color: white;
  position: relative;
}
.footer__list-link::after {
  left: 0;
  top: 0;
  transition: all 0.2s linear;
}
.footer__list-link--phone::after {
  width: 18px;
  height: 18px;
  background: url(../images/icon-phone.svg) no-repeat;
}
.footer__list-link--email::after {
  width: 20px;
  height: 16px;
  background: url(../images/icon-email.svg) no-repeat;
}
.footer__menu {
  display: flex;
  justify-content: space-between;
  flex-shrink: 3;
  flex-basis: 230px;
}
.footer__menu-item {
  margin-bottom: 20px;
}
.footer__menu-link {
  color: white;
  transition: all 0.2s linear;
}
.footer__menu-link:hover {
  font-weight: 700;
}
.footer__social {
  display: flex;
}
.footer__social-item {
  margin-right: 15px;
}
.footer__social-item:last-child {
  margin-right: 0;
}
.footer__social-link {
  display: block;
  width: 33px;
  height: 33px;
  border: 1px solid white;
  border-radius: 50%;
  position: relative;
  transition: all 0.2s linear;
}
.footer__social-link svg {
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 14px;
  transform: translate(-50%, -50%);
  transition: all 0.2s linear;
}
.footer__social-link:hover {
  border-color: #65e2d9;
}
.footer__social-link:hover svg {
  color: #65e2d9;
}

@media (max-width: 768px) {
  .header__logo-img {
    width: 73px;
    height: 23px;
  }

  .menu__list-item + .menu__list-item {
    margin-left: 30px;
  }

  .access__inner {
    padding: 35px 25px;
  }

  .access__input-email {
    margin-right: 20px;
  }

  .features__items {
    grid-template-columns: auto;
  }

  .productive__inner {
    flex-direction: column;
  }

  .productive__img {
    max-width: 511px;
    margin-bottom: 40px;
  }

  .productive__title {
    max-width: none;
  }

  .productive__content {
    max-width: none;
    margin-left: 0;
  }

  .productive {
    padding: 65px 0 117px;
  }

  .header {
    background: #1C2230 url(../images/bg-curvy-mobile.svg) bottom/100% 220px no-repeat;
  }
}
@media (max-width: 600px) {
  .access__form {
    flex-direction: column;
  }

  .access__input-email {
    margin: 0 0 13px 0;
    max-width: none;
  }

  .access__btn {
    width: 100%;
  }

  .testimonials {
    padding-bottom: 140px;
  }

  .footer__logo-img {
    width: 157px;
    height: 47px;
  }

  .footer__items {
    display: block;
  }

  .footer__location {
    display: inline-block;
    margin-bottom: 24px;
  }

  .footer__social {
    justify-content: center;
  }

  .footer__menu {
    justify-content: flex-start;
    grid-gap: 30px;
    flex-basis: auto;
    margin: 60px 0;
  }

  .features__items {
    grid-template-columns: 250px;
    grid-gap: 65px;
  }
}
@media (max-width: 375px) {
  .testimonials__text {
    font-size: 12px;
  }

  .title {
    font-size: 18px;
  }

  body {
    font-size: 13px;
  }

  .header_logo-img {
    width: 160px;
    height: 47px;
  }

  .footer__menu {
    flex-direction: column;
  }

  .header__text {
    font-size: 14px;
  }

  .header__btn {
    padding: 16px 73px;
  }
}